<template>
  <div class="history-div">
    <v-data-table
    :headers="headers"
    :items="delivered_tanks_list"
    :items-per-page="5"
    class="elevation-1"
    @click:row="openDialog"
  ></v-data-table>
  <v-dialog
        v-if="dialog"
        v-model="dialog"
        fullscreen
        hide-overlay
        transition="dialog-bottom-transition"
        scrollable
      >
        <v-card tile>
          <v-toolbar
            flat
            dark
            color="primary"
          >
            <v-btn
              icon
              dark
              @click="dialog = false;"
            >
              <v-icon>mdi-close</v-icon>
            </v-btn>
            <v-toolbar-title>Zbiornik</v-toolbar-title>
            <v-spacer></v-spacer>
          </v-toolbar>
          <v-card-text>
            <TankForm :formData="formData" :action="action" parcelId="0" slotId="0"/>
          </v-card-text>
        </v-card>
      </v-dialog>
  </div>
</template>
<script>
import TankForm from '@/components/Home/TankForm.vue';
import {mapState} from 'vuex';
import axios from 'axios';
export default {
  name: 'History',
  components: {
    // Parcel,
    TankForm,
  },
  data() {
    return{
      action: 'history',
      formData:{},
      dialog: false,
      headers: [
          {
            text: 'Nazwa',
            align: 'start',
            sortable: false,
            value: 'name',
          },
          { text: 'Miasto', value: 'city' },
          { text: 'Ulica', value: 'street' },
        ],
    }
  },
  created() {
    if (!this.$store.getters['authentication/isLoggedIn']) {
      this.$router.push('/login');
    }
    this.$store.dispatch('common/loadDeliveredTanksList');
  },
  methods:{
    openDialog(e){
      console.log(e);
      axios.get(`${this.api_url}tank/get/${e.id}`, {
          headers: {
            Authorization: this.token,
          //   apitoken: context.rootState.apitoken,
          //   clientbrowser: Vue.clientbrowser,
          },
        })
        .then((result)=>{
          // Object.assign({}, this.tankData, result.data[0])
          this.formData = result.data[0];
          this.dialog = true;
        })
      // this.formData = e;
      // this.dialog = true;
    }
  },
  computed: {
    ...mapState('common', [
      'delivered_tanks_list',
    ]),
    ...mapState('authentication', [
      'user',
      'token',
    ]),
    ...mapState([
      'api_url'
    ]),
  }
}
</script>